.product-container {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
      'Helvetica Neue', 'Ubuntu', sans-serif;
    height: 60vh;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .product-section {
    background: #ffffff;
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 112px;
    border-radius: 6px;
    justify-content: space-between;
  }
  
  .product {
    display: flex;
  }
  
  .description {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .product-title,
  .product-price {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.154px;
    color: #242d60;
    margin: 0;
  }
  
  .product-price {
    opacity: 0.5;
  }
  
  .product-image {
    border-radius: 6px;
    margin: 10px;
    width: 54px;
    height: 57px;
  }
  
  .btn {
    height: 36px;
    background: linear-gradient(#af2655, #864774);
    color: white;
    width: 100%;
    font-size: 14px;
    border: 0;
    font-weight: 500;
    cursor: pointer;
    letter-spacing: 0.6;
    border-radius: 0 0 6px 6px;
    transition: all 0.2s ease;
    box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
  }
  
  .checkout-btn:hover {
    opacity: 0.8;
    background: #af2655;
  }
  